/* QABot.css */
.qabot-container {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 200px;
  background-color: #1a0dab;
  color: white;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebar-item {
  width: 80%;
  padding: 10px;
  margin: 10px 0;
  background-color: #1a0dab;
  color: white;
  border: none;
  text-align: center;
  cursor: pointer;
}

.sidebar-item.beta {
  background-color: #ff0000;
  position: relative;
}

.sidebar-item.beta span {
  background-color: #ff0000;
  color: white;
  padding: 2px 5px;
  font-size: 0.7em;
  position: absolute;
  top: 0;
  right: 0;
}

.main-content {
  flex-grow: 1;
  padding: 20px;
  text-align: center;
}

.main-content h1 {
  font-size: 24px;
  color: #1a237e;
  font-weight: 700;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e9ecef;
  text-align: center;
}

p {
  color: #000000;
  font-size: 16px;
  margin-bottom: 20px;
}

.options {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.option {
  border: 2px solid #0000ff;
  padding: 20px;
  width: 250px;
  border-radius: 5px;
  text-align: left;
}

.get-started {
  background-color: #06A700;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
}

@media (max-width: 768px) {
  .qabot-container {
    flex-direction: column;
  }
  
  .sidebar {
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  
  .sidebar-item {
    width: auto;
    margin: 0 5px;
    padding: 8px 12px;
    font-size: 14px;
  }
  
  .options {
    flex-direction: column;
    align-items: center;
  }
  
  .option {
    width: 100%;
    max-width: 300px;
  }
  
  .main-content h1 {
    font-size: 20px;
  }
  
  p {
    font-size: 14px;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .main-content h1 {
    font-size: 22px;
  }
  
  p {
    font-size: 15px;
  }
}