/* ResearchBook.css */
.research-book-section {
  min-height: 100vh;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  align-items: center;
}

.hero-text {
  text-align: center;
}

.section-title {
  font-size: 24px;
  color: #1a237e;
  font-weight: 700;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e9ecef;
}

.section-subtitle {
  font-size: 1.1rem;
  color: #475569;
  margin-bottom: 0.8rem;
  line-height: 1.5;
  font-weight: 400;
}

.section-description {
  font-size: 0.95rem;
  color: #64748b;
  margin-bottom: 1.5rem;
  line-height: 1.5;
}

.action-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.btn {
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 0.375rem;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
}

.btn-primary {
  background: linear-gradient(135deg, #2563eb 0%, #1d4ed8 100%);
  color: white;
  box-shadow: 0 2px 4px rgba(37, 99, 235, 0.2);
}

.btn-primary:hover {
  background: linear-gradient(135deg, #1d4ed8 0%, #1e40af 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 6px rgba(37, 99, 235, 0.3);
}

.btn-primary:active {
  transform: translateY(0);
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.research-book-visual {
  width: 320px;
  height: 240px;
  background: linear-gradient(135deg, #e0e7ff 0%, #c7d2fe 100%);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6366f1;
  font-size: 0.9rem;
  font-weight: 500;
}

@media (min-width: 768px) {
  .hero-content {
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
  }
  
  .hero-text {
    text-align: left;
  }
  
  .action-buttons {
    justify-content: flex-start;
  }
  
  .section-title {
    font-size: 2.25rem;
  }
  
  .section-subtitle {
    font-size: 1.2rem;
  }
}

@media (min-width: 1024px) {
  .research-book-section {
    padding: 3rem 2rem;
  }
  
  .section-title {
    font-size: 2.5rem;
  }
  
  .section-subtitle {
    font-size: 1.3rem;
  }
}

@media (max-width: 767px) {
  .research-book-section {
    padding: 1.5rem;
  }
  
  .section-title {
    font-size: 1.75rem;
  }
  
  .section-subtitle {
    font-size: 1rem;
  }
  
  .section-description {
    font-size: 0.9rem;
  }
  
  .btn {
    padding: 0.75rem 1.25rem;
    font-size: 0.9rem;
  }
  
  .research-book-visual {
    width: 280px;
    height: 210px;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 1.5rem;
  }
  
  .section-subtitle {
    font-size: 0.95rem;
  }
  
  .research-book-visual {
    width: 240px;
    height: 180px;
  }
  
  .action-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .btn {
    width: 100%;
    justify-content: center;
  }
}

/* Tablet View */
@media (min-width: 481px) {
  .section-title {
    font-size: 26px;
  }
}

/* Desktop View */
@media (min-width: 769px) {
  .section-title {
    font-size: 28px;
  }
}

/* Large Desktop View */
@media (min-width: 1025px) {
  .section-title {
    font-size: 30px;
  }
}

/* Mobile Responsive */
@media (max-width: 480px) {
  .section-title {
    font-size: 22px;
  }
}